/* this is to make the navbar text flash green.
.gameStatusCorrect {
  animation-name: flash_green;
  animation-duration: 1s;
  animation-timing-function: ease-in-out;
  animation-delay: 0s;
}*/

.gameStatusIncorrect {
  -webkit-animation-name: flash_red;
          animation-name: flash_red;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
}
  
@-webkit-keyframes flash_red {
  0% {
    color: white;
  }
  50% {
    color: red;
  }
  100% {
    color: white;
  }
}
  
@keyframes flash_red {
  0% {
    color: white;
  }
  50% {
    color: red;
  }
  100% {
    color: white;
  }
}
@-webkit-keyframes flash_green {
  0% {
    color: white;
  }
  50% {
    color: green
  }
  100% {
    color: white;
  }
}
@keyframes flash_green {
  0% {
    color: white;
  }
  50% {
    color: green
  }
  100% {
    color: white;
  }
}

